.hamburger__line, .hamburger::after, .hamburger::before {
  content: "";
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 4px;
  transition: all 0.4s ease;
  border-radius: 10px;
}

.hamburger {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 23px;
  margin-right: 53px;
  transition: all 0.8s;
  z-index: 3;
}
.hamburger::before {
  background-color: white;
  top: 0;
  left: 0;
}
.hamburger::after {
  background-color: white;
  bottom: 0;
  left: 0;
}

.hamburger__line {
  background-color: white;
  top: calc(50% - 2px);
  left: 0;
}

.hamburger:hover {
  cursor: pointer;
}

.hamburger-active {
  transform: rotate(90deg);
  transition: all 0.8s;
  position: absolute;
  top: 10px;
  left: 100px;
  z-index: 3;
}

.overlay {
  position: fixed;
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s ease 0s;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

.overlay-active {
  visibility: visible;
  opacity: 0;
  z-index: 1;
}

.fixed-position {
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

img {
  display: block;
}

html {
  font-size: 10px;
}

body {
  display: flex;
  align-items: none;
  justify-content: space-between;
  flex-direction: column;
  font-size: 1.6rem;
  min-height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -moz-user-select: none;
  background: #BE93C5;
  background: -webkit-linear-gradient(to right, #7BC6CC, #BE93C5);
  background: linear-gradient(to right, #7BC6CC, #BE93C5);
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px 60px;
  height: 100%;
}

.card {
  width: calc(100%/4 - 40px);
  min-width: 220px;
  height: 220px;
  perspective: 800px;
  transition: all 0.7s;
  margin: 0 10px 20px 10px;
  border-radius: 19px;
}
.card__img {
  height: 80%;
  width: 100%;
  object-fit: cover;
  border-top-left-radius: 19px;
  border-top-right-radius: 19px;
  transition: all 0.5s;
}
.card__img_play {
  height: 100%;
}
.card__description {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 20%;
  padding: 10px;
  color: #37053d;
}
.card__description_play {
  display: none;
}
.card__button {
  border: none;
  background: none;
  background-size: cover;
  width: 15px;
  height: 15px;
}
.card__button:hover, .card__button:active, .card__button:focus {
  border: none;
  outline: none;
}
.card__button:hover {
  animation: circle 0.7s ease-out;
  transition: 0.7s ease-out;
  cursor: pointer;
  transform: scale(1.01);
}
.card__button_play {
  display: none;
}

.card__wrapper {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  position: relative;
  transition: transform 1s;
}

.flip-card {
  transform: rotateY(180deg);
}

.card__side_back,
.card__side_front {
  backface-visibility: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: white;
  box-shadow: 1px 3px 4px rgba(0, 0, 0, 0.1);
  border-radius: 19px;
  transition: all 0.2s;
  overflow: hidden;
}
.card__side_back:hover,
.card__side_front:hover {
  box-shadow: 1px 3px 20px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

.card__side_front:hover {
  transform: scale(1.01);
}

.card__side_back {
  transform: rotateY(180deg);
}

@keyframes circle {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
.switch-btn {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 95px;
  height: 38px;
  border-radius: 19px;
  background: #13b6bf;
  z-index: 0;
  padding: 0 8px;
  cursor: pointer;
  position: relative;
  transition-duration: 0.5s;
  text-transform: uppercase;
  color: white;
  box-shadow: inset 0px -1px 1px rgba(255, 255, 255, 0.28), inset 0px 1px 2px rgba(0, 0, 0, 0.25);
}
.switch-btn::after {
  content: "";
  height: 32px;
  width: 32px;
  border-radius: 17px;
  background: white;
  position: absolute;
  top: 3px;
  left: 3px;
  z-index: 1;
  transition-duration: 0.3s;
}

.switch-on {
  background: #4978c7;
  justify-content: flex-start;
}
.switch-on::after {
  left: 60px;
}

.play-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 95px;
  height: 38px;
  border-radius: 19px;
  background: #13b6bf;
  z-index: 0;
  padding: 0 8px;
  cursor: pointer;
  position: relative;
  transition-duration: 0.5s;
  text-transform: uppercase;
  color: white;
  box-shadow: inset 0px -1px 1px rgba(255, 255, 255, 0.28), inset 0px 1px 2px rgba(0, 0, 0, 0.25);
}

.play -on {
  background: #4978c7;
  justify-content: flex-start;
}
.play -on::after {
  left: 60px;
}

.wrapper__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer {
  background-color: #2d7e90;
  color: white;
  height: 50px;
  font-size: 1rem;
}

.wrapper__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  justify-content: flex-start;
  padding-top: 10px;
  padding-bottom: 10px;
}

.logo img {
  width: 80px;
  height: 25px;
}
.logo__github {
  margin: 0 10px;
}
.logo__github img {
  width: 25px;
  height: 25px;
}

.navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.5rem;
  line-height: 160%;
  letter-spacing: none;
  font-style: normal;
  font-weight: normal;
  list-style-type: none;
  padding: 0;
  color: white;
  position: fixed;
  left: 0;
  top: 0;
  margin-top: 0;
  height: 100vh;
  text-align: left;
  flex-direction: column;
  align-items: flex-start;
  width: 320px;
  transform: translateX(-100%);
  -webkit-transform: translateX(-100%);
  -moz-transform: translateX(-100%);
  background-color: #753b90;
  transition: all 0.5s ease-in;
  padding: 100px 20px 100px 70px;
  opacity: 1;
  z-index: 2;
}
.navigation li {
  margin: 5px 0;
}
.navigation li:hover {
  transform: scale(1.05);
  transition: all 0.3s;
}
.navigation li a {
  color: white;
  text-decoration: none;
  font-size: 2.5rem;
}

.active {
  padding-bottom: 3px;
  border-bottom: 3px solid white;
}

.navigation-active {
  opacity: 1;
  transform: translateX(0%);
  -webkit-transform: translateX(0%);
  -moz-transform: translateX(0%);
}

.wrapper__main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  min-height: 600px;
  padding: 20px 50px;
}